import './App.css';
import React from 'react';
import ListItem from './listItem'
import {library} from '@fortawesome/fontawesome-svg-core';
import {faTrash} from '@fortawesome/free-solid-svg-icons';

library.add(faTrash)

class App extends React.Component{
  constructor(props){
    super(props)
    this.state={
      items:[],
      currItem:{
        text:"",
        key:""
      }
    }
  }
  handleInput=(event)=>{
    this.setState({
        currItem:{
          text: event.target.value,
          key:Date.now()
        }
    })
  }

  addItem=(event)=>{
    event.preventDefault();
    const newItem=this.state.currItem;
    console.log(newItem);
    if(newItem.text!==""){
    const newItems=[...this.state.items,newItem];
    this.setState({
      items:newItems,
      currItem:{
        text:"",
        key:""
      }
      }
    )
  }
  }
  deleteItem=(key)=>{
    const filterItems=this.state.items.filter(item=>item.key!==key);
    this.setState({
      items:filterItems
    }
    )
  }

  itemUpdate=(text,key)=>{
    const items=this.state.items;
    items.map(item=>{
      if(item.key===key){
        item.text=text;
      }
    })
    this.setState({
      items:items
    })
  }
  render(){
      return (
        <div className="todoApp">
         <header>
          <form id="form_id" onSubmit={this.addItem} >
            <input type="text" placeholder="Enter Text" value={this.state.currItem.text} onChange={this.handleInput}/>
            <button type="submit" >Add</button>
          </form>
        </header>
        <ListItem items={this.state.items} deleteItem={this.deleteItem} setUpdate={this.itemUpdate}>

        </ListItem>
        </div>


      )

  }
}

export default App;
   